.logo {

  $block-name:                &; // #{$block-name}__element

  display: inline-block;
  vertical-align: top;
  white-space: nowrap;

  img {
    display: block;
    width: 62px;
    height: 62px;

    @media (min-width: $screen-md) {
      width: 82px;
      height: 82px;
    }
  }
}
