.content-block {
  margin-top: -60px;
  padding-bottom: 40px;

  @media (min-width: $screen-md) {
    padding-bottom: 60px;
  }

  $block-name:                &; // #{$block-name}__element

  // Вакакнсии и т.п. //

  &__main-col {
    @include col(xs, 12, $grid-columns, 20px);
    @include col(md, 12, $grid-columns, 30px);
    @include col(lg, 12);
    @include col(xl, 8);
    margin-bottom: 40px;

    @media (min-width: $screen-md) {
      margin-bottom: 60px;
    }

    @media (min-width: $screen-xl) {
      margin-bottom: 0;
    }
  }

  &__aside-col {
    @include col(xs, 12, $grid-columns, 20px);
    @include col(md, 12, $grid-columns, 30px);
    @include col(lg, 12);
    @include col(xl, 4);
  }

  // Контакты //

  &__contacts-col {
    @include col(xs, 12, $grid-columns, 20px);
    @include col(md, 6, $grid-columns, 30px);
    @include col(lg, 6);
    @include col(xl, 6);
    margin-bottom: 40px;

    @media (min-width: $screen-md) {
      margin-bottom: 0;
    }
  }

  &__map-col {
    @include col(xs, 12, $grid-columns, 20px);
    @include col(md, 6, $grid-columns, 30px);
    @include col(lg, 6);
    @include col(xl, 6);
    display: flex;
    flex-direction: column;
  }

  &__map-img {
    margin-bottom: 20px;
    border-radius: $border-radius;

    @media (min-width: $screen-md) {
      margin-bottom: 30px;
    }

    // @media (min-width: $screen-xl) {
    //   margin-bottom: 40px;
    // }
  }

  &__map {
    flex: 1 1 auto;
    overflow: hidden;
    border-radius: $border-radius;

    iframe {
      width: 100%;
      height: 100%;
      min-height: 325px;
      border: none;

      @media (min-width: $screen-lg) {
        min-height: 0;
      }
    }
  }

  &__article {
    padding: 30px 10px;
    background-color: $white;
    border-radius: $border-radius;
    box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.15);

    @media (min-width: $screen-md) {
      padding-left: 20px;
      padding-right: 20px;
    }

    @media (min-width: $screen-xl) {
      padding: 40px;
    }

    & + & {
      margin-top: 40px;

      @media (min-width: $screen-md) {
        margin-top: 60px;
      }
    }

    h2,
    h3,
    h4 {
      margin-top: 1.5em;
    }

    h2:first-child {
      margin-top: 0;
    }

    img {
      display: block;
      width: 100%;
      margin: 2em 0;
    }
  }

  &__big-title {
    padding-bottom: 30px;
    margin-bottom: 30px;
    border-bottom: 1px solid $border-color;

    @media (min-width: $screen-md) {
      padding-bottom: 40px;
      margin-bottom: 40px;
    }

    & > img {
      display: inline-block;
      vertical-align: top;
      max-width: 100px;
      margin-top: 0;
      margin-bottom: 30px;
      margin-right: 20px;

      @media (min-width: $screen-md) {
        max-width: 120px;
        margin-right: 30px;
        margin-bottom: 0;
      }

      @media (min-width: $screen-xl) {
        margin-right: 40px;
      }
    }
  }

  &__more-title {
    display: inline-block;
    vertical-align: top;
    margin: 0 !important;

    & > span {
      display: block;
      margin-bottom: 20px;
      font-size: 16px;
      color: $main-color;
    }
  }
}
